import React from 'react';
import TopPage from './TopPage';
import ProblemPage from './ProblemPage'
import { BrowserRouter, Routes, Route } from "react-router-dom";

import '../css/App.css';


export default　function App() {
    const titleList ={
      data: [
      "1章 バンディット問題",
      "2章 マルコフ決定過程",
      "3章 ベルマン方程式",
      "4章 動的計画法",
      "5章 モンテカルロ法",
      "6章 TD法",
      "7章 ニューラルネットとQ学習",
      "8章 DQN",
      "9章 方策勾配法",
      "10章 さらに先へ",
  ]
}

  const pages = []
  for(let i=0; i <titleList.data.length; i++){
    const p = <Route path={`/chapter${i+1}/`} element={<ProblemPage chapter={i+1} title={titleList.data[i]}/>} />
    pages.push(p)
  }

  return (
    <BrowserRouter>
      <Routes>
        <Route path={`/`} element={<TopPage titleList={titleList} />} />
        {pages}
      </Routes>
    </BrowserRouter>


    // <div className="App"> 
    //   <TopPage clickHandler={chapterSelect}/>
    // </div>
  );
}