import React from "react"
import { Link } from "react-router-dom";
import check_gray_path from '../images/check_gray.png';
import check_green_path from '../images/check_green.png';

export default function ChapterButton(props) {
    return (
        
        <div className="chapter-face">
            <img src={props.isClear ? check_green_path : check_gray_path} className="check_top-icon" alt="correct icon" />
            <Link style={{ textDecoration: 'none', color: "#3B3936"}} to={`/chapter${props.value}/`}>
                <p className="chapter-num">{props.title}</p>
            </Link>
        </div>
        
    )
}