import React from "react"
import cover_path from '../images/cover.jpg';
import ChapterButton from "./ChapterButton";
import Footer from "./Footer";
import '../css/TopPage.css';


export default function TopPage(props) {
    const chapterNum = 10
    
    const titleList = props.titleList.data
    const [clearList, setclearList] = React.useState(
        () => {
            const __clearList = []

            for(let i=0; i <chapterNum; i++){
                const b = Boolean(parseInt(localStorage.getItem(`book4-chapter${i+1}-isclear`))) || false
                __clearList.push(b)
            }
            return __clearList
        }
    )

    const chapterButtonDom = []
    for(let i=0; i <chapterNum; i++){
        chapterButtonDom.push(
        <ChapterButton key={i} isClear={clearList[i]} value={i+1} title={titleList[i]} clickHandler={props.clickHandler}/>
        )
    }

    return (
        <div className="main-container">
            <div className="top-container">
                <div className="top-txt-container">
                    <a href="https://www.amazon.co.jp/dp/4873119758/ref=cm_sw_r_tw_dp_7E324ZWVC18MSY0DRJ1G" target="_blank">
                        <img src={cover_path} className="cover-img" alt="book cover" />
                    </a>
                    <h2 className="top-title">確認テスト100題</h2>
                    <a href="https://www.amazon.co.jp/dp/4873119758/ref=cm_sw_r_tw_dp_7E324ZWVC18MSY0DRJ1G" target="_blank"><p className="top-subtitle">『ゼロから作るDeep Learning ❹ ―強化学習編』</p></a>
                    <a id="come-btn1" href="https://docs.google.com/forms/d/e/1FAIpQLSdsuFU_3h1nUlheZU8fF1kFNQRXMK6EMrPuOQicIW8lV7GuSg/viewform" target="_blank">
                    <button className="btn btn-toppage">問題案を募集しています</button>
                    </a>
                </div> 
                <div className="top-chapter-container">
                        {chapterButtonDom }
                </div>

                <a id="come-btn2" href="https://docs.google.com/forms/d/e/1FAIpQLSdsuFU_3h1nUlheZU8fF1kFNQRXMK6EMrPuOQicIW8lV7GuSg/viewform" target="_blank">
                    <button className="btn btn-toppage">問題案を募集しています</button>
                </a>
            </div>
            <Footer />
        </div>
    )
}