import React from "react"
import '../css/App.css';
import '../css/ProblemPage.css';


export default function SelectLable(props) {
    const {lazyCheckList, checkList, answer, handleChange, text, id, value} = props

    return (
        <div className={!lazyCheckList ? "p-label" : 
                    (answer[id] === value) ? "p-label p-correct" :
                    (lazyCheckList[id] === answer[id]) && lazyCheckList[id] === value ? "p-label p-correct" : (lazyCheckList[id] !== answer[id]) && lazyCheckList[id] === value ? "p-label p-wrong fadeout" : "p-label fadeout"
                    }>
                    <label><input
                        // className="radio-input"
                        type="radio" 
                        name={id}
                        value={value}
                        checked={checkList[id] === value}
                        onChange={handleChange}
                        // required
                        disabled={lazyCheckList!==undefined}
                    />
                    <div className="selection-raido-txt">
                        {/* {text} */}
                        <div style={{display:"inline"}}dangerouslySetInnerHTML={{__html: text}} />

                    </div>
                    </label>
        </div>
    )
}